@import url('https://fonts.googleapis.com/css2?family=DotGothic16&display=swap');

$gradientColorBlue: #1010ff;
$gradientColorRed : #ff1010;
$gradientColorPink: #ee10ff;

.topPage {
    position: relative;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;

    @media (min-width: 481px){
        height: 100vh;
    }

    .titleContainer{
        position: relative;
        background-color: #abe471;
        padding: 10px 20px;
        text-align: center;
        top: -30%;
        margin-top: 30px;
        height: auto;
        z-index: 5;

        .title {
            position: relative;
            left: 50%;
            transform: translateX(-50%);
            background: linear-gradient(to right, $gradientColorBlue 20%, $gradientColorPink, $gradientColorRed 30%, $gradientColorRed 70%, $gradientColorPink, $gradientColorBlue 80%);
            background-size: 200% 100%;
            background-clip: text;
            -webkit-background-clip: text;
            color: transparent;
            -webkit-text-fill-color: transparent;
            color: transparent;
            animation: gradientAnimation 10s linear infinite;
            font-size: 7vh;
            z-index: 10;
            text-transform: capitalize;
            text-align: center;
            font-family: "DotGothic16", sans-serif;
            font-weight: 400;
            font-style: normal;
            font-size: 3rem;
            font-weight: bold;
            margin: 0;
            line-height: 4rem;
        }
    }

    .sk_img {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
        width: 100vw;
        object-fit: cover;
        top:50%;
        left: 50%;
        transform: translate(-50%, -50%);
        
        img {
            max-height: 100vh;
        }
    }
    
    .startButton {
        position: absolute;
        bottom: 30px;
        display: inline-block;
        padding: 10px 20px;
        background: linear-gradient(to right, $gradientColorRed 20%, $gradientColorPink, $gradientColorBlue 30%, $gradientColorBlue 70%, $gradientColorPink, $gradientColorRed 80%);
        background-size: 200% 100%;
        color: white;
        animation: gradientAnimation 10s linear infinite;
        text-decoration: none;
        border: none;
        border-radius: 5px;
        text-align: center;
        font-family: "DotGothic16", sans-serif;
        font-weight: 400;
        font-style: normal;
        font-size: 3vh;
        font-weight: bold;
        z-index: 10;
    }
}

@keyframes gradientAnimation {
    0% {
        background-position: 0% 0%;
    }
    100% {
        background-position: 200% 0%;
    }
}

@keyframes reverseGradientAnimation {
    0% {
        background-position: 200% 0%;
    }
    100% {
        background-position: 0% 0%;
    }
}